import { ArrowBackIcon } from "@chakra-ui/icons";
import { Badge, Divider, Flex, IconButton, Link } from "@chakra-ui/react";
import React, { ComponentType, PropsWithChildren, useState } from "react";
import useStore from "../store";

const BackButton: React.FC<PropsWithChildren> = ({ children }) => {
    const { page, setPage } = useStore(({ page, setPage }) => ({ page, setPage }))
    const [isMouseOver, setMouseOver] = useState(false)

    if (!page) {
        return null
    }

    return (
        <>
            <IconButton
                isRound
                boxSize={8}
                variant='outline'
                colorScheme=''
                aria-label='Navigation backwards'
                icon={<ArrowBackIcon boxSize={4} />}
                onClick={() => setPage(undefined)}
                bgColor={isMouseOver ? 'blue.700' : ''}
                onMouseEnter={() => setMouseOver(true)}
                onMouseLeave={() => setMouseOver(false)}
            />
            <Divider
                orientation='vertical'
                boxSize={6}
                ml={2}
                w='5px'
            />
        </>
    )
}


const printProps = (Component: ComponentType<PropsWithChildren>) => {
    return (props: PropsWithChildren) => {
        return <Component {...props} />
    }

}





const withHover = (Component: ComponentType<PropsWithChildren>, timeout: number) => {

    return (props: PropsWithChildren) => {

        return <Component {...props} />
    }
}













const BackButtonWrapped = printProps(BackButton)

export default function Header() {
    return (
        <Flex
            p={2}
            flexDirection='row'
            alignItems='center'
        >

            <BackButtonWrapped />

            <Link href='/'>
                <Badge bgColor='transparent' colorScheme="white">unityhints.dev</Badge>
            </Link>
        </Flex>
    )
}
