import React, { PropsWithChildren, useContext, useEffect } from "react"
import StartPage from "../pages/StartPage"
import Collider from "../posts/Collider"
import useStore from "../store"
import usePage from "../hooks/usePage"

type QueryParam = 'page'

type PageContextValue = {
    page: string
}

const PageContext = React.createContext({} as PageContextValue)

const getQueryParam = (name: QueryParam) => {
    const params = new URLSearchParams(window.location.search)
    return params.get(name)
}

const getPageComponent = (page: string) => {
    return <Collider />
}

export const PageProvider: React.FC<PropsWithChildren> = ({ children }) => {
    const { page, setPage } = usePage()

    useEffect(() => {
        const pageParam = getQueryParam('page')
        if (pageParam) {
            setPage(pageParam)
            getPageComponent(pageParam)
        }
    }, [])

    return (
        <>
            {!page
                ? (<StartPage />)
                : (getPageComponent(page))
            }
        </>
    )
}

export const usePageContext = () => useContext(PageContext)

export type PageIndexProps = {
    page: string
    component: any
}
