import { SearchIcon } from "@chakra-ui/icons";
import { Badge, Input, InputGroup, InputLeftAddon, InputRightElement } from "@chakra-ui/react";
import { useRef, useState } from "react";
import { usePageContext } from "../contexts/PageContext";

export default function Search() {
    const [search, setSearch] = useState('')
    const input = useRef<HTMLInputElement>(null)
    const page = usePageContext()

    return (
        <InputGroup>
            <InputLeftAddon>
                <Badge>unityhints.dev</Badge>
            </InputLeftAddon>
            <Input
            onClick={() => console.log(page.page)}
                ref={input}
                placeholder='Search...'
                onKeyDown={e => {
                    if (e.key === 'Escape' && input.current) {
                        input.current.value = ''
                        return
                    }

                    setSearch(e.currentTarget.value)
                }}
            />
            <InputRightElement>
                <SearchIcon color='grey.300' />
            </InputRightElement>
        </InputGroup>
    )
}
