import React, { PropsWithChildren } from "react"
import { ChakraProvider } from "./ChakraContext"
import { ReactQueryProvider } from "./ReactQueryContext"

export const ContextBaseProvider: React.FC<PropsWithChildren> = ({ children }) => {
    return (
        <ReactQueryProvider>
            <ChakraProvider>
                {children}
            </ChakraProvider>
        </ReactQueryProvider>
    )
}
